<template>
<div>
  {{cols}}
  <el-table
  :data="tableData"
  border
  style="width: 100%; text-align:center">
    <el-table-column
      v-for="col in cols"
      :key="col"
      :prop="col.prop"
      :label="col.label" >
    </el-table-column>
  </el-table>

  <el-button raw-type="button" @click="addCol">
    
  </el-button>
  <el-button raw-type="button" @click="delCol">
    
  </el-button>
</div>
</template>
<script>
export default {

  data () {
    return {
      tableData: [{
        date: '2016-05-02',
        name: '',
        address: ' 1518 '
      }, {
        date: '2016-05-04',
        name: '',
        address: ' 1517 '
      }, {
        date: '2016-05-01',
        name: '',
        address: ' 1519 '
      }, {
        date: '2016-05-03',
        name: '',
        address: ' 1516 '
      }],
      cols: [
        {prop: 'date', label: ''},
        {prop: 'name', label: ''}
      ]
    }
  },
  methods: {
    addCol () {
      this.cols.push({prop: 'address', label: ''})
      this.tableData.push(
        {
          date: '2016-05-03',
          name: '',
          address: ' 1516 '
        }
      )
    },
    delCol () {
      console.log(this.cols.length)
      this.cols.splice((this.cols.length - 2), 2)
      this.tableData.splice(this.tableData.length - 1, 1)
    }
  }
}
</script>
<style>
  .cell {
    text-align: center !important
  }
</style>



