    nationFun () {
      // this.stdform.gradeIdName=this.arradmin.gradeIdName

      this.placeOfOriginArr=this.stdform.homeAddress.split(',')
      this.registeredResidenceArr=this.stdform.registeredResidence.split(',')
      this.nativeHeathArr=this.stdform.nativeHeath.split(',')
      this.placeOfJgArr=this.stdform.placeOfOrigin.split(',')
     //this.Disability=this.stdform.is


      // 
      var national = [
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', ''
      ]
      var json = {}
      var nationArr = []
      for (var i = 0; i < national.length; i++) {
        json = {}
        json.value = national[i]
        json.label = national[i]
        nationArr.push(json)
      }
      this.nationSelect = nationArr
      // 
      this.$http.get('/api/v1/region/region')
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.placeOfOriginSelect = res.data.data
            } else {
              this.$notify({
                title: '',
                message: '',
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
        // 
      var guojia = [
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''
      ]
      var guojiaJson = {}
      var guojiaArr = []
      for (var i = 0; i < guojia.length; i++) {
        guojiaJson = {}
        guojiaJson.value = guojia[i]
        guojiaJson.label = guojia[i]
        guojiaArr.push(guojiaJson)
      }
      this.guoJiaSelect = guojiaArr
      // 
      var periodJson = {}
      var periodArr = []
      this.$http.get('/api/v1/common/findAllSchoolPeriod')
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              console.log(res.data.data)
              // for (var i = 0; i < res.data.data.length; i++) {
              //   periodJson = {}
              //   periodJson.value = res.data.data[i].periodId
              //   periodJson.label = res.data.data[i].periodName
              //   periodArr.push(periodJson)
              // }
              this.periodIdSelect = res.data.data
            } else {
              this.$notify({
                title: '',
                message: '',
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 
    queryGradeFun () {
      console.log(this.stdform.periodId)
      // this.stdform.gradeId = ''
      var gradeJson = {}
      var gradeArr = []
      this.$http.get('/api/v1/common/findAllClassByGradeAndPeriodId?periodId=' + this.stdform.periodId)
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              console.log(res.data.data)
              this.gradeIdSelect = res.data.data
            } else {
              this.queryClassFun()
            }
          } else {
            this.$notify({
              title: '',
              message: '',
              type: 'error'
            })
          }
        })
        .catch(err => {
          console.log(err)
        })
        // this.stdform.belongToYear=''
        // this.stdform.gradeId=''
        // this.stdform.classId=''
        this.queryGradeNameFun()
    },
    // 
    queryClassFun () {
      // this.stdform.classId = ''
      var classObj = {
        'periodId': this.stdform.periodId,
        'thetime': this.stdform.belongToYear

      }
      this.$http.post('/api/v1/common/findAllSchoolClassByThetime', classObj)
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.classIdSelect = res.data.data
            } else {
              this.$notify({
                title: '',
                message: '',
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
        // this.stdform.classId=''
        // this.stdform.gradeId=''
    },
    //
     queryGradeNameFun(){
      this.$http.get('/api/v1/schoolperiod/findInfo')
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              for(let i=0;i<res.data.data.length;i++){
                if(this.stdform.periodId===res.data.data[i].periodId){
                  this.gradeNameSelect=res.data.data[i].gradeList
                }
              }
            } else {
              this.$notify({
                title: '',
                message: '',
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })

    },
    queryGradeNameFunOther(){

    },
    /**
     * 
     */
    classSub () {
      if (this.stdform.periodId === '') {
        this.$message.error('')
        return false
      }
      // if (this.stdform.gradeId === '') {
      //   this.$message.error('')
      //   return false
      // }
      if (this.stdform.classId === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.gender === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.birthday === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.registerNumber === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.dateOfAdmission === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.classYear === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.belongToYear === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.namePinyin === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.politicalOutlook === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.nation === '') {
        this.$message.error('')
        return false
      }
      if (this.placeOfOriginArr === '') {
        this.$message.error('')
        return false
      }
      if (this.registeredResidenceArr === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.differentCountrie === '') {
        this.$message.error('/')
        return false
      }
      if (this.placeOfJgArr === '') {
        this.$message.error('')
        return false
      }
      if (this.nativeHeathArr === '') {
        this.$message.error('')
        return false
      }
      // if (this.postalAddress === '') {
      //   this.$message.error('')
      //   return false
      // }
      // if(this.Disability.toString===''){
      //   this.stdform.isDisability='0'
      // }else{
      //   this.stdform.isDisability='1'
      // }
      if (this.placeOfOriginArr !== '') {
        this.stdform.homeAddress = this.placeOfOriginArr.toString()
      }
      if (this.registeredResidenceArr !== '') {
        this.stdform.registeredResidence = this.registeredResidenceArr.toString()
      }
      if (this.nativeHeathArr !== '') {
        this.stdform.nativeHeath = this.nativeHeathArr.toString()
      }
      if (this.placeOfJgArr!== '') {
        this.stdform.placeOfOrigin = this.placeOfJgArr.toString()
      }
      var arradmin = {}
      for (var item in this.stdform) {
        if (this.stdform[item]) {
          arradmin[item] = this.stdform[item]
        }
      }
      console.log('arradmin', arradmin)
      this.$http
        .put(
          '/api/v1/studentManagement/updateStaffInfo',
          arradmin
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.$notify({
                title: '',
                message: '',
                type: 'success'
              })
              // popover
              this.isUpdate = true
              this.hideDialog()
            } else {
              this.$notify({
                title: '',
                message: res.data.code,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    }
  },
  watch: {
    'stdInfoRowData' (cur, old) {
      this.stdform = {}
      this.stdform = cur

      this.nationFun()
      this.queryGradeFun()
      this.queryClassFun()
      this.stdform.birthDate = formatDate(new Date(this.stdform.birthDate), 'yyyy-MM-dd')
      this.stdform.dateOfAdmission = formatDate(new Date(this.stdform.dateOfAdmission), 'yyyy-MM-dd')
      console.log('this.stdform', this.stdform, this.stdform.birthDate, this.stdform.dateOfAdmission)
    }
  },
  computed: {
    updateHeadUrl () {
      let url = '/api/v1/common/uploadFile'
      const baseURL = this.$http.defaults.baseURL
      const ans = baseURL ? baseURL + url : url
      return ans
    }
  }
}
</script>
