        periodId: '', // pk-
        periodIdName: '', //
        photoUrl: '', // 
        placeOfOrigin: '', // 
        politicalOutlook: '', // (01  02  03 04  05  06  07  08  09  10  11  12  13)
        postalAddress: '', // 
        preschoolEducation: '', // (0-1-)
        presentAddress: '', // 
        registerAssistNumber: '', // 
        registerNumber: '', // 
        registerState: '', // 0-1-
        registeredResidence: '', // 
        registeredResidenceDetailed: '', // -
        schoolNumber: '', // 
        schoolState: '', // 0-1-
        state: '', // 0-1-1
        studentSource: '', // 
        studentState: '', // 
        studentType: '', // 
        studentsComeIn: '', // 
        thisCityOsaas: '', // (0-1-)
        treatType: '', // (0-1-
        userHead: '', // 
        userId: '', // ID
        userNike: '', // 
        userUuid: '', // UUID
        wayOfStudying: '', // 
        workForChildren: '', // (0-1-)
        zipAddress: '', // 
        zipCode: '' // 
      },
      stdformCopy: {},
      // 
      marriageSelect: [{
        value: '1',
        label: ''
      }, {
        value: '0',
        label: ''
      }],
      // 
      nationSelect: [],
      // 
      placeOfOriginSelect: [],
      placeOfOriginArr: [], // 
      registeredResidenceArr: [], // 
      nativeHeathArr: [], // 
      placeOfJgArr: [], // 
      guoJiaSelect: [], // 
      periodIdSelect: [], // 
      classIdSelect: [], // 
      gradeIdSelect:[],//
      gradeNameSelect: [], // 
      isUpdate: false
    }
  },
  mounted () {
    this.nationFun()
    this.stdformCopy = JSON.parse(JSON.stringify(this.stdform))
    // this.getTimeList()
  },
  computed: {
    updateHeadUrl () {
      let url = '/api/v1/common/uploadFile'
      const baseURL = this.$http.defaults.baseURL
      const ans = baseURL ? baseURL + url : url
      return ans
    }
  },
  methods: {
    hideDialog () {
      this.stdform = this.stdformCopy
      this.$emit('hideDialog')
      if (this.isUpdate) {
        this.isUpdate = false
        this.$emit('updateTable')
      }
      this.placeOfJgArr = []
      this.nativeHeathArr = []
      this.placeOfOriginArr = []
      this.registeredResidenceArr = []
    },
    handleAvatarSuccess (res, file) {
      // this.imageUrl = URL.createObjectURL(file.raw)
      this.stdform.photoUrl = res.data
      console.log(res)
      console.log(file.raw)
      console.log(this.stdform.photoUrl)
      this.imageUrl = true
    },
    beforeAvatarUpload (file) {
      // const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2
      // if (!isJPG) {
      //   this.$message.error(' JPG !');
      // }
      if (!isLt2M) {
        this.$message.error(' 2MB!')
      }
      // return isJPG && isLt2M;
      return isLt2M
    },
    // 
    nationFun () {
      // 1234567
      // 1~4

      // 
      var national = [
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', ''
      ]
      var json = {}
      var nationArr = []
      for (var i = 0; i < national.length; i++) {
        json = {}
        json.value = national[i]
        json.label = national[i]
        nationArr.push(json)
      }
      this.nationSelect = nationArr
      // 
      this.$http.get('/api/v1/region/region')
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.placeOfOriginSelect = res.data.data
            } else {
              this.$notify({
                title: '',
                message: '',
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
        // 
      var guojia = [
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''
      ]
      var guojiaJson = {}
      var guojiaArr = []
      for (var i = 0; i < guojia.length; i++) {
        guojiaJson = {}
        guojiaJson.value = guojia[i]
        guojiaJson.label = guojia[i]
        guojiaArr.push(guojiaJson)
      }
      this.guoJiaSelect = guojiaArr
      // 

      this.$http.get('/api/v1/common/findAllSchoolPeriod')
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              console.log('', res.data.data)
              this.periodIdSelect = res.data.data
            } else {
              this.$notify({
                title: '',
                message: '',
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
        this.stdform.belongToYear=''
        this.stdform.gradeId=''
        this.stdform.classId=''
    },
    // 
    queryGradeFun () {
      console.log(this.stdform.periodId)
      this.$http.get('/api/v1/common/findAllClassByGradeAndPeriodId?periodId=' + this.stdform.periodId)
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              console.log(res.data.data)
              this.gradeIdSelect = res.data.data
            } else {
              this.$notify({
                title: '',
                message: '',
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
        this.stdform.belongToYear=''
        this.stdform.gradeId=''
        this.stdform.classId=''
        this.queryGradeNameFun()
    },
    // 
    queryClassFun () {
      var classObj = {
        'periodId': this.stdform.periodId,
        'thetime': this.stdform.belongToYear
      }
      this.$http.post('/api/v1/common/findAllSchoolClassByThetime', classObj)
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.classIdSelect = res.data.data
            } else {
              this.$notify({
                title: '',
                message: '',
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
        this.stdform.classId=''
        this.stdform.gradeId=''
    },
    //
    queryGradeNameFun(){
      // this.stdform.gradeId=''
      this.$http.get('/api/v1/schoolperiod/findInfo')
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              for(let i=0;i<res.data.data.length;i++){
                if(res.data.data[i].periodId==this.stdform.periodId){
                  this.gradeNameSelect=res.data.data[i].gradeList
                }
              }
            } else {
              this.$notify({
                title: '',
                message: '',
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    queryGradeNameFunOther(){
    },
    /**
     * 
     */
    classSub () {
      if (this.stdform.periodId === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.classId === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.gender === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.birthDate === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.registerNumber === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.dateOfAdmission === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.classYear === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.belongToYear === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.namePinyin === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.politicalOutlook === '') {
        this.$message.error('')
        return false
      }
      if (this.stdform.nation === '') {
        this.$message.error('')
        return false
      }

      if (this.stdform.differentCountries === '') {
        this.$message.error('/')
        return false
      }
      if (this.placeOfJgArr === '') {
        this.$message.error('')
        return false
      }
      if (this.nativeHeathArr === '') {
        this.$message.error('')
        return false
      }
      if (this.placeOfOriginArr === '') {
        this.$message.error('')
        return false
      }
      if (this.registeredResidenceArr === '') {
        this.$message.error('')
        return false
      }

      // if(this.disability===''){
      //   this.stdform.isDisability=0
      // }else{
      //   this.stdform.isDisability=1
      // }
      if (this.placeOfOriginArr.length!=='') {
        this.stdform.homeAddress = this.placeOfOriginArr.toString()
      }
      if (this.registeredResidenceArr.length !=='') {
        this.stdform.registeredResidence = this.registeredResidenceArr.toString()
      }
      if (this.nativeHeathArr.length !=='') {
        this.stdform.nativeHeath = this.nativeHeathArr.toString()
      }
      if (this.placeOfJgArr.length !=='') {
        this.stdform.placeOfOrigin = this.placeOfJgArr.toString()
      }
      let arradmin = {}
      for (var item in this.stdform) { // valer=null
        if (this.stdform[item]) {
          // if(this.stdform[item].length>0){
          //
          // }
          arradmin[item] = this.stdform[item]
        }
      }
      this.$http.post('/api/v1/studentManagement/addStudentInfo', arradmin)
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.$notify({
                title: '',
                message: '',
                type: 'success'
              })
              this.isUpdate = true
              this.$emit('updateTable')
              // 
              // this.stdform = this.stdformCopy
              // this.placeOfJgArr = []
              // this.nativeHeathArr = []
              // this.placeOfOriginArr = []
              // this.registeredResidenceArr = []
              // this.Disability=[]
              // popover
              this.hideDialog()
            } else {
              this.$notify({
                title: '',
                message: res.data.code,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    /**  */
    regitsterBtnClick () {
      this.$http
        .get(
          '/api/v1/studentManagement/getStudentNumber'
        )
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.stdform.registerNumber = res.data.data
            } else {
              console.log(res.data.msg)
              this.$message({
                //  ,toast
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    }

  }
}
</script>
