<template>
  <div id="main" style="width: 600px;height:400px;"></div>
</template>
<script>
export default {
  data () {
    return {

    }
  },
  methods: {
    drawLine () {
      var myChart = this.$echarts.init(document.getElementById('main'))
      // 
      var option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: { // 
            type: 'shadow' // 'line' | 'shadow'
          }
        },
        legend: {
          data: ['', '', '']
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        yAxis: [
          {
            type: 'value'
          }
        ],
        xAxis: [
          {
            type: 'category',
            axisTick: {show: false},
            data: ['', '', '', '', '', '', '']
          }
        ],
        series: [
          {
            name: '',
            type: 'bar',
            label: {
              normal: {
                show: true,
                position: 'inside'
              }
            },
            data: [3, 5, 2, 4, 3, 20]
          },
          {
            name: '',
            type: 'bar',
            stack: '',
            label: {
              normal: {
                show: true
              }
            },
            data: [ null, 4, null, null, 5, 6 ]
          },
          {
            name: '',
            type: 'bar',
            stack: '',
            label: {
              normal: {
                show: true,
                position: 'left'
              }
            },
            data: [-3, null, -1, -4, null, null]
          }
        ]
      }
      myChart.setOption(option)
    }
  },
  mounted () {
    this.drawLine()
  }
}
</script>



