          this.midifyObj[item] = ''
        }
      }
    },
    handleAvatarSuccess (res, file) {
      // this.imageUrl = URL.createObjectURL(file.raw)
      this.midifyObj.adminHead = res.data
      console.log(res)
      console.log(file.raw)
      console.log(this.midifyObj.adminHead)
      this.imageUrl = true
    },
    // 
    hideFun () {
      this.$emit('update:addfunFlaf', false)
    },
    // 

    beforeAvatarUpload (file) {
      // const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2
      // if (!isJPG) {
      //   this.$message.error(' JPG !');
      // }
      if (!isLt2M) {
        this.$message.error(' 2MB!')
      }
      // return isJPG && isLt2M;
      return isLt2M
    },
    //
    // subjectFind(){
    //   console.log("nihao")
    //   // this.$http.get('/api/v1/schoolsubject/findSubjectInfo')
    //   // .then(res => {
    //   //     if (res.status === 200) {
    //   //       if (res.data.code === 1) {
    //   //         this.theSubjectSelect=res.data.data
    //   //       } else {
    //   //         this.$message({
    //   //           showClose: true,
    //   //           message: res.data.msg,
    //   //           type: 'error'
    //   //         })
    //   //       }
    //   //     }
    //   //   })
    //   //   .catch(err => {
    //   //     console.log(err)
    //   //   })

    // },
    //
    handleChange(){
      this.$http.get('/api/v1/region/region')
      .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.placeOfOriginSelect=res.data.data
            } else {
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    submitForm () {
      var flagPop = this.modify.flag

      if (this.midifyObj.fullName === '') {
        this.$message.error('')
        return false
      }
      if (this.midifyObj.staffNumber === '') {
        this.$message.error('')
        return false
      }
      if (this.midifyObj.adminType === '') {
        this.$message.error('')
        return false
      }
      if (this.midifyObj.birthDate === '') {
        this.$message.error('')
        return false
      }
      if (this.midifyObj.englishName === '') {
        this.$message.error('')
        return false
      }
      if (this.midifyObj.gender === '') {
        this.$message.error('')
        return false
      }

      if (this.placeOfOriginArr !== '') {
        this.midifyObj.placeOfOrigin = this.placeOfOriginArr.toString()
      }
      var arradmin = {}
      console.log(this.midifyObj.birthDate)
      console.log(flagPop)
      if (flagPop === 1) {
        if (this.midifyObj.theDepartmentStr) {
          console.log(this.midifyObj.theDepartmentStr)
          var result = this.midifyObj.theDepartmentStr.split(',')
          this.midifyObj.theDepartmentId = result[0]
          this.midifyObj.theDepartment = result[1]
          console.log(result)
          console.log(this.midifyObj.theDepartmentId)
          console.log(this.midifyObj.theDepartment)
          delete this.midifyObj.theDepartmentStr
        }
        for (var item in this.midifyObj) { // valer=null
          if (this.midifyObj[item]) {
            // if(this.midifyObj[item].length>0){
            //
            // }
            arradmin[item] = this.midifyObj[item]
          }
        }
        this.$http.post('/api/v1/personnelManagement/addStaff', arradmin)
          .then(res => {
            if (res.status === 200) {
              if (res.data.code === 1) {
                this.$emit('refreshList')
                this.$notify({
                  title: '',
                  message: '',
                  type: 'success'
                })

                this.hideFun()
              } else {
                this.$notify({
                  title: '',
                  message: res.data.code,
                  type: 'error'
                })
              }
            }
          })
          .catch(err => {
            console.log(err)
          })
      } else {
        // 
        if (this.midifyObj.theDepartmentStr) {
          delete this.midifyObj.theDepartmentStr
        }
        for (var item in this.midifyObj) { // valer=null
          if (this.midifyObj[item]) {
            arradmin[item] = this.midifyObj[item]
          }
        }
        this.$http.put('/api/v1/personnelManagement/updateStaffInfo', arradmin)
          .then(res => {
            // console.log(res)
            console.log('res.data', res)
            if (res.status === 200) {
              if (res.data.code === 1) {
                this.$emit('refreshList')
                this.$notify({
                  title: '',
                  message: '',
                  type: 'success'
                })
                this.hideFun()
              } else {
                this.$notify({
                  title: '',
                  message: '',
                  type: 'error'
                })
              }
            }
          })
          .catch(err => {
            console.log(err)
          })
      }
    },
    // resetForm(formName) {
    //   this.$refs[formName].resetFields()
    // },
    // 
    numberFun () {
      this.$http.get('/api/v1/personnelManagement/createIdentifier')
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 0) {
              this.midifyObj.staffNumber = res.data.data
            } else {
              this.$notify({
                title: '',
                message: '',
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    // 
    nationFun () {
      // 
      var national = [
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', '', '', '', '', '', '',
        '', '', '', '', '', '', '', '', '', ''
      ]
      var json = {}
      var nationArr = []
      for (var i = 0; i < national.length; i++) {
        json = {}
        json.value = national[i]
        json.label = national[i]
        nationArr.push(json)
      }
      this.nationSelect = nationArr
      // 
      this.$http.get('/api/v1/region/region')
        .then(res => {
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.placeOfOriginSelect = res.data.data
            } else {
              this.$notify({
                title: '',
                message: '',
                type: 'error'
              })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
      // 
      var departmentArr = []
      var departmentJson = {}
      this.$http.get('/api/v1/personnelManagement/findAllDepartmentInfo')
        .then(res => {
          // console.log(res)
          if (res.status === 200) {
            // this.$store.commit('setToken',res.data);
            // localStorage.userName = this.userInfo.userName;
            // localStorage.token_expire = res.data.expire;
            // localStorage.token = res.data.token;
            if (res.data.code === 1) {
              console.log(res.data.data)
              for (var i = 0; i < res.data.data.length; i++) {
                departmentJson = {}
                departmentJson.value = res.data.data[i].id + ',' + res.data.data[i].dName
                departmentJson.label = res.data.data[i].dName
                departmentArr.push(departmentJson)
              }
              this.theDepartmentSelectObj = departmentArr
            } else {
              // this.$notify({
              //   title: '',
              //   message: '',
              //   type: 'error'
              // })
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
        //
        this.$http.get('/api/v1/schoolsubject/findSubjectInfo')
        .then(res => {
            if (res.status === 200) {
              if (res.data.code === 1) {
                this.theSubjectSelect=res.data.data
              } else {
                this.$message({
                  showClose: true,
                  message: res.data.msg,
                  type: 'error'
                })
              }
            }
          })
          .catch(err => {
            console.log(err)
          })
    }
  },
  computed: {
    updateHeadUrl () {
      let url = '/api/v1/common/uploadFile'
      const baseURL = this.$http.defaults.baseURL
      const ans = baseURL ? baseURL + url : url
      return ans
    }
  }

}
</script>

<style lang="scss">
  .popupBox{
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.5);
    z-index: 15;
    /*display: none;*/
  }
.departmentPopup{
  width: 850px;
  height: 760px;
  background-color: #ffffff;
  border-radius: 10px 10px;
  box-shadow: 0px 0px 6px #c4c8ca;
  position: absolute;
  left: 50%;
  right: 50%;
  top: 50%;
  bottom: 50%;
  margin: -385px -425px;
  .departmentPopTitBox{
    height: 50px;
    line-height: 50px;
    background-color: #6bc08b;
    text-align: center;
    color: #fff;
    font-size: 18px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    img{
      vertical-align: middle;

    }
  }

  .departmentPopImg{
    width: 140px;
    height: 140px;
    line-height: 140px;
    text-align: center;
    font-size: 28px;
    border: 1px dashed #d9d9d9;
    border-radius: 50%;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    margin: 15px auto;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  .departmentPopImgbox{
    background-color: #fafcfc;
    padding: 10px 15px;
    p{
      width: 80px;
      height: 25px;
      border-radius:5px ;
      border:1px solid #6bc08b;
      display: block;
      background-color: transparent;
      color: #6bc08b;
      font-size: 12px;
      margin: auto;
      cursor: pointer;
    }
  }
  .departmentPopCont{
    padding: 25px 30px 0 30px;
    max-height: 640px;
    overflow: auto;
  }
  .departmentPopContBx{
    border: 1px solid #eeeeee;
    border-radius: 4px;
    margin-bottom: 10px;
  }
  .departmentPopContTit{
    width: 60px;
    height: 20px;
    color: #fff;
    font-size: 12px;
    background-color: #b26afe;
    text-align: center;
    line-height: 20px;
    margin-bottom: 10px;
  }
  .el-input__inner{
    height: 25px;
    line-height: 25px;
    background-color: #f2f2f2;
  }
  .el-input{
    font-size: 12px;

  }
  .el-form-item__content{
    line-height: 25px;
    margin-left: 150px !important;

  }
  .el-form-item__label{
    font-size: 12px;
    height: 26px;
    line-height: 26px;
    width: 150px !important;
  }
  .el-form-item{
    margin-bottom: 12px;
  }
  .depatPopupBtn{
    width: 100%;
    text-align: center;
    margin: 15px 0;
    button{
      border-radius: 25px;
      border-color: #6bc08b;
      color: #6bc08b;
      padding: 8px 30px;
    }
    button:hover,button:active,button:visited,button:focus{
      background-color: #6bc08b;
      color: #fff;
    }
  }
  #popoupBtn{
    color: #6bc08b;
    height: 40px;
    line-height: 40px;
    display: block;
    padding: 0 15px;
  }
  .el-input__icon{
    line-height: 25px;
  }
  .el-cascader{
    font-size: 12px;
    line-height: 25px;
  }
  .el-date-editor.el-input, .el-date-editor.el-input__inner{
    width: 100%;
  }
  .el-input.is-disabled .el-input__inner{
    height: 25px !important;
  }
  .el-form-item.is-required .el-form-item__label:before{
    content: "*";
    color: #f56c6c;
    margin-right: 4px;
  }
}

</style>



